/* src/components/GamifiedPromoBanner.css */
.gamified-banner {
  position: fixed !important;
  bottom: 30px !important;
  right: 30px !important;
  left: auto !important;
  width: 360px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 12px;
  box-shadow: 0 15px 30px rgba(102, 126, 234, 0.3);
  padding: 20px;
  transition: all 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
  opacity: 0;
  transform: translateY(30px) scale(0.9);
  z-index: 1000;
  overflow: hidden;
  position: relative;
  color: white !important;
}

.gamified-banner h3,
.gamified-banner p,
.gamified-banner span:not(.gamified-banner-badge) {
  color: white !important;
}

.gamified-banner-visible {
  opacity: 1;
  transform: translateY(0) scale(1);
}

/* Email collection variant */
.gamified-banner-email {
  padding: 24px;
}

/* Close button */
.gamified-banner-close {
  position: absolute;
  top: 10px;
  right: 10px;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: white;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s;
  font-size: 11px;
  z-index: 2;
}

.gamified-banner-close:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: rotate(90deg);
}

/* Header */
.gamified-banner-header {
  color: white;
  margin-bottom: 16px;
}

.gamified-banner-title {
  font-size: 17px;
  font-weight: 700;
  margin: 0 0 6px 0;
  color: white !important;
}

.gamified-banner-subtitle {
  font-size: 13px;
  margin: 0;
  opacity: 0.95;
  color: white;
}

/* Progress bar */
.gamified-banner-progress {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  padding: 3px;
  margin-bottom: 16px;
  position: relative;
}

.progress-bar {
  height: 6px;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 4px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #52c41a 0%, #73d13d 100%);
  border-radius: 4px;
  transition: width 0.5s ease;
  box-shadow: 0 0 8px rgba(82, 196, 26, 0.5);
}

.progress-text {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 10px;
  color: white;
  font-weight: 600;
}

/* Tasks */
.gamified-banner-tasks {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 14px;
}

.task-item {
  display: flex;
  flex-direction: column;
  gap: 8px;
  background: rgba(255, 255, 255, 0.15);
  padding: 10px 14px;
  border-radius: 8px;
  transition: all 0.3s;
  border: 2px solid transparent;
}

.task-item.active {
  background: rgba(255, 255, 255, 0.25);
  border-color: rgba(255, 255, 255, 0.4);
  animation: glow 2s ease-in-out infinite;
}

.task-item > div:first-child {
  display: flex;
  align-items: center;
  gap: 10px;
}

.task-help-text {
  font-size: 11px;
  color: rgba(255, 255, 255, 0.8);
  margin-left: 30px;
  line-height: 1.4;
  font-style: italic;
}

@keyframes glow {
  0%, 100% { box-shadow: 0 0 0 rgba(255, 255, 255, 0); }
  50% { box-shadow: 0 0 15px rgba(255, 255, 255, 0.3); }
}

.task-item.completed {
  background: rgba(82, 196, 26, 0.2);
  border-color: rgba(82, 196, 26, 0.4);
}

.task-item.locked {
  opacity: 0.6;
}

.task-icon {
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.lock-icon {
  font-size: 14px;
  filter: grayscale(1);
}

.task-content {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
}

.task-title {
  font-size: 13px;
  font-weight: 500;
  color: white;
}

.task-item.completed .task-title {
  text-decoration: line-through;
  opacity: 0.8;
}

.task-hint {
  font-size: 11px;
  opacity: 0.8;
  margin-left: 6px;
  color: white;
}

.task-action-button {
  background: rgba(255, 255, 255, 0.25);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.4);
  padding: 3px 10px;
  border-radius: 12px;
  font-size: 11px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.task-action-button:hover {
  background: rgba(255, 255, 255, 0.35);
  transform: translateX(2px);
}

/* CTA Button */
.gamified-banner-cta {
  margin-top: 14px;
}

.cta-button {
  width: 100%;
  background: linear-gradient(135deg, #52c41a 0%, #73d13d 100%);
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  box-shadow: 0 4px 12px rgba(82, 196, 26, 0.4);
  animation: pulse 2s ease-in-out infinite;
}

.cta-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(82, 196, 26, 0.5);
}

.cta-note {
  margin: 6px 0 0 0;
  font-size: 11px;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
}

/* Animations */
@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

/* Confetti */
.confetti-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
  z-index: 10;
}

.confetti {
  position: absolute;
  width: 8px;
  height: 8px;
  background: #ffd700;
  animation: confetti-fall 3s linear forwards;
}

.confetti-0 { left: 10%; background: #ff6b6b; animation-delay: 0s; }
.confetti-1 { left: 20%; background: #4ecdc4; animation-delay: 0.2s; }
.confetti-2 { left: 30%; background: #45b7d1; animation-delay: 0.4s; }
.confetti-3 { left: 40%; background: #f9ca24; animation-delay: 0.6s; }
.confetti-4 { left: 50%; background: #f0932b; animation-delay: 0.8s; }
.confetti-5 { left: 60%; background: #eb4d4b; animation-delay: 1s; }

@keyframes confetti-fall {
  0% {
    top: -10%;
    transform: translateX(0) rotateZ(0deg);
    opacity: 1;
  }
  100% {
    top: 110%;
    transform: translateX(100px) rotateZ(720deg);
    opacity: 0;
  }
}

/* Responsive */
@media (max-width: 640px) {
  .gamified-banner {
    right: 15px !important;
    bottom: 15px !important;
    left: 15px !important;
    width: auto;
  }
  
  .gamified-banner-title {
    font-size: 16px;
  }
  
  .task-item {
    padding: 10px 12px;
  }
}

/* Builder page adjustments */
.gamified-banner.builder-page {
  bottom: 80px !important;
}

.gamified-banner-minimized.builder-page {
  bottom: 80px !important;
}

.email-title {
  font-size: 18px;
  font-weight: 700;
  margin: 0 0 8px 0;
  color: white !important;
}

.email-subtitle {
  font-size: 13px;
  opacity: 0.9;
  margin: 0 0 16px 0;
  color: white;
}

.email-form {
  display: flex;
  gap: 8px;
}

.email-input {
  flex: 1;
  padding: 10px 14px;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  background: rgba(255, 255, 255, 0.15);
  color: white !important;
  font-size: 13px;
  transition: all 0.3s;
}

.email-input::placeholder {
  color: rgba(255, 255, 255, 0.7) !important;
}

.email-input:focus {
  outline: none;
  background: rgba(255, 255, 255, 0.25);
  border-color: rgba(255, 255, 255, 0.5);
}

.email-submit {
  padding: 10px 16px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.25);
  border: 1px solid rgba(255, 255, 255, 0.4);
  color: white;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
  white-space: nowrap;
}

.email-submit:hover {
  background: rgba(255, 255, 255, 0.35);
}

.email-privacy {
  font-size: 10px;
  color: rgba(255, 255, 255, 0.7);
  margin-top: 8px;
}

.email-privacy a {
  color: rgba(255, 255, 255, 0.9);
  text-decoration: underline;
}

/* Minimized view */
.gamified-banner-minimized {
  position: fixed !important;
  bottom: 30px !important;
  right: 30px !important;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 25px;
  padding: 8px 16px;
  cursor: pointer;
  box-shadow: 0 8px 20px rgba(102, 126, 234, 0.3);
  transition: all 0.3s;
  z-index: 1000;
}

.gamified-banner-minimized:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(102, 126, 234, 0.4);
}

.minimized-content {
  display: flex;
  align-items: center;
  gap: 10px;
  color: white;
}

.minimized-icon {
  font-size: 16px;
}

.minimized-text {
  font-size: 13px;
  font-weight: 600;
}

.minimized-progress {
  width: 80px;
  height: 4px;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 2px;
  overflow: hidden;
}

.minimized-progress-fill {
  height: 100%;
  background: #52c41a;
  transition: width 0.5s ease;
}

.minimized-count {
  font-size: 11px;
  opacity: 0.9;
}