/**
 * WP AI Workflows - Workflow Viewer Styles
 */

/* Container styles */
.wp-ai-workflows-viewer-container {
  position: relative;
  min-height: 300px;
  border: 1px solid #e0e0e0;
  border-radius: 15px;
  overflow: hidden;
  margin: 0px 0;
  background-color: #f9f9f9;
}

/* Loading indicator */
.workflow-viewer-loading {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.9);
  z-index: 10;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #1677ff;
  border-radius: 50%;
  animation: spin 1.5s linear infinite;
  margin-bottom: 10px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-text {
  font-size: 14px;
  color: #666;
}

/* Error display */
.workflow-viewer-error {
  padding: 20px;
  text-align: center;
  color: #721c24;
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 4px;
  margin: 10px 0;
}

/* Make sure ReactFlow fills the container */
.wp-ai-workflows-viewer-container .react-flow {
  width: 100%;
  height: 100%;
}

/* Dark mode adjustments */
.wp-ai-workflows-viewer-container.dark-mode {
  border-color: #333;
  background-color: #1f1f1f;
}

.wp-ai-workflows-viewer-container.dark-mode .workflow-viewer-loading {
  background: rgba(31, 31, 31, 0.9);
}

.wp-ai-workflows-viewer-container.dark-mode .loading-text {
  color: #ccc;
}

.wp-ai-workflows-viewer-container.dark-mode .loading-spinner {
  border-color: #333;
  border-top-color: #1677ff;
}

/* Download button styles */
.workflow-download-button {
  position: absolute !important;
  top: 10px !important;
  right: 10px !important;
  z-index: 1000 !important; /* Ensure it's above other elements */
  display: flex !important;
  background: rgba(255, 255, 255, 0.8) !important;
  padding: 8px !important;
  border-radius: 4px !important;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15) !important;
  transition: all 0.2s ease !important;
  pointer-events: auto !important;
}

.workflow-download-button:hover {
  background: rgba(255, 255, 255, 0.95) !important;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2) !important;
}

.dark-mode .workflow-download-button {
  background: rgba(40, 40, 40, 0.8) !important;
}

.dark-mode .workflow-download-button:hover {
  background: rgba(50, 50, 50, 0.95) !important;
}

.workflow-download-button button {
  cursor: pointer !important;
  pointer-events: auto !important;
}

/* Toolbar styles */
.workflow-viewer-toolbar {
  position: absolute !important;
  top: 10px !important;
  right: 10px !important;
  z-index: 1000 !important;
  display: flex !important;
  gap: 8px !important;
  background: rgba(255, 255, 255, 0.8) !important;
  padding: 6px !important;
  border-radius: 4px !important;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1) !important;
  pointer-events: auto !important;
}

/* If both toolbar and download button are present, position download below toolbar */
.workflow-viewer-toolbar + .workflow-download-button {
  top: 60px !important;
}

/* Make node buttons non-interactive in the viewer */
.workflow-viewer-container .react-flow__node button {
  pointer-events: none !important;
  cursor: default !important;
}

/* Override ReactFlow styles */
.wp-ai-workflows-viewer-container .react-flow__minimap {
  bottom: 10px;
  right: 10px;
}

.wp-ai-workflows-viewer-container .react-flow__controls {
  bottom: 10px;
  left: 10px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.1);
}

/* Ensure download button is visible on all backgrounds */
.workflow-download-button button {
  min-width: 100px !important;
  padding: 5px 15px !important;
  font-weight: 500 !important;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .wp-ai-workflows-viewer-container {
    height: 400px !important; /* Force reasonable height on mobile */
  }
  
  .workflow-download-button {
    top: 10px !important;
    right: 10px !important;
  }
}