/* frontend/src/styles/LockedNodeStyles.css */

/* ==== NEW LOCKED NODE CONTAINER STYLES ==== */

.locked-node-container {
  position: relative;
  margin: 4px;
}

/* Pro Feature Banner */
.pro-feature-banner {
  position: absolute;
  top: -45px;
  left: 0;
  right: 0;
  z-index: 15;
  background: linear-gradient(135deg, #ff4d4f, #ff7875);
  border-radius: 8px 8px 0 0;
  box-shadow: 0 2px 8px rgba(255, 77, 79, 0.3);
  animation: slideDown 0.3s ease-out;
}

.pro-banner-content {
  display: flex;
  align-items: center;
  padding: 8px 12px;
  gap: 8px;
  color: white;
}

.pro-banner-icon {
  font-size: 18px;
  color: #fff;
  flex-shrink: 0;
}

.pro-banner-text {
  flex: 1;
  min-width: 0;
}

.pro-banner-title {
  display: block;
  color: white !important;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.2;
  margin-bottom: 2px;
}

.pro-banner-description {
  display: block;
  color: rgba(255, 255, 255, 0.9) !important;
  font-size: 11px;
  line-height: 1.3;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.pro-banner-button {
  flex-shrink: 0;
  height: 28px !important;
  padding: 0 12px !important;
  font-size: 11px !important;
  font-weight: 600 !important;
  background: rgba(255, 255, 255, 0.2) !important;
  border: 1px solid rgba(255, 255, 255, 0.3) !important;
  border-radius: 4px !important;
  color: white !important;
  transition: all 0.2s ease !important;
}

.pro-banner-button:hover {
  background: rgba(255, 255, 255, 0.3) !important;
  border-color: rgba(255, 255, 255, 0.5) !important;
  transform: translateY(-1px) !important;
}

/* Locked Node Content */
.locked-node-content {
  position: relative;
  border: 2px solid #ff4d4f;
  border-radius: 8px;
  background: rgba(255, 77, 79, 0.02);
  margin-top: 0;
  overflow: hidden;
}

/* Remove handles from locked nodes */
.locked-node-content .react-flow__handle {
  display: none !important;
}

/* Only disable form inputs and workflow-related controls, not navigation/UI controls */
.locked-node-content .ant-input,
.locked-node-content .ant-select,
.locked-node-content .ant-textarea,
.locked-node-content .ant-input-number,
.locked-node-content .ant-checkbox,
.locked-node-content .ant-radio,
.locked-node-content .ant-switch,
.locked-node-content .ant-slider,
.locked-node-content .ant-color-picker,
.locked-node-content .ant-upload,
.locked-node-content .ant-form-item-control {
  pointer-events: none !important;
  opacity: 0.6;
}

/* Keep these interactive elements working */
.locked-node-content .ant-card-head,
.locked-node-content .ant-card-head *,
.locked-node-content .ant-tabs-nav,
.locked-node-content .ant-tabs-nav *,
.locked-node-content .ant-tabs-tab,
.locked-node-content .ant-collapse-header,
.locked-node-content .ant-collapse-header *,
.locked-node-content .ant-btn[class*="delete"],
.locked-node-content .ant-btn[class*="close"],
.locked-node-content .anticon-delete,
.locked-node-content .anticon-close,
.locked-node-content .ant-popconfirm,
.locked-node-content .ant-tooltip {
  pointer-events: auto !important;
  opacity: 1 !important;
}

/* Specifically ensure delete and close buttons work */
.locked-node-content .ant-card-extra,
.locked-node-content .ant-card-extra * {
  pointer-events: auto !important;
  opacity: 1 !important;
}

/* Add subtle overlay only to form areas, not the entire node */
.locked-node-content .ant-card-body::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 77, 79, 0.02);
  pointer-events: none;
  z-index: 0;
  border-radius: 0 0 6px 6px;
}

/* Ensure card body is positioned for the overlay */
.locked-node-content .ant-card-body {
  position: relative;
}

/* Make sure content stays above the overlay */
.locked-node-content .ant-card-body > * {
  position: relative;
  z-index: 1;
}

/* Animation for the banner */
@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ==== SIMPLE MENU ITEM STYLES ==== */

/* No special styling needed - just let the (PRO) text show normally */
.ant-menu-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* ==== LOCKED FEATURE STYLES (Unchanged) ==== */

.locked-feature-block {
  position: relative;
  margin: 8px 0;
  border-radius: 6px;
  overflow: hidden;
}

.locked-feature-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  z-index: 5;
  border-radius: 4px;
  border: 1px dashed #d9d9d9;
  min-height: 60px;
  gap: 4px;
}

.locked-feature-content {
  opacity: 0.4;
  pointer-events: none;
}

.locked-feature-text {
  font-size: 12px;
  text-align: center;
  padding: 0 8px;
  color: #666;
  margin: 4px 0;
}

/* ==== INLINE LOCKED FEATURES (Unchanged) ==== */

.locked-feature-inline {
  color: #999;
  cursor: not-allowed;
  display: inline-flex;
  align-items: center;
}

.inline-lock-icon {
  font-size: 12px;
  margin-right: 4px;
  color: #1890ff;
}

.locked-text {
  text-decoration: line-through;
  opacity: 0.6;
}

/* ==== PRO ONLY CONFIG STYLES (Unchanged) ==== */

.pro-only-config {
  background: linear-gradient(135deg, #f0f8ff 0%, #e6f4ff 100%);
  padding: 16px;
  border-radius: 6px;
  border: 1px dashed #1890ff;
  text-align: center;
  margin: 12px 0;
}

.pro-only-config .anticon {
  font-size: 24px;
  color: #1890ff;
  margin-bottom: 8px;
  display: block;
}

/* ==== RESPONSIVE STYLES ==== */

@media (max-width: 768px) {
  .pro-feature-banner {
    top: -40px;
  }
  
  .pro-banner-content {
    padding: 6px 10px;
    gap: 6px;
  }
  
  .pro-banner-icon {
    font-size: 16px;
  }
  
  .pro-banner-title {
    font-size: 11px;
  }
  
  .pro-banner-description {
    font-size: 10px;
  }
  
  .pro-banner-button {
    height: 24px !important;
    padding: 0 8px !important;
    font-size: 10px !important;
  }
}

@media (max-width: 480px) {
  .pro-feature-banner {
    top: -35px;
  }
  
  .pro-banner-content {
    padding: 5px 8px;
    gap: 4px;
  }
  
  .pro-banner-description {
    display: none; /* Hide description on very small screens */
  }
}

/* ==== DARK MODE SUPPORT ==== */

@media (prefers-color-scheme: dark) {
  .locked-node-content {
    background: rgba(255, 77, 79, 0.05);
  }
  
  .locked-node-content::after {
    background: rgba(255, 77, 79, 0.08);
  }
  
  .pro-only-config {
    background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
    border-color: #4a90e2;
  }
}